.a {
  display: flex;
  height: 100vh;
  align-items: center;
}
.a-left {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100%;
}

.a-card {
  width: 60%;
  height: 70vh;
  border-radius: 30px;
  position: absolute;
  overflow: hidden;
}

.a-img {
  width: 60%;
  height: 60%;
  position: relative;
  border-radius: 15px 50px 35px;
  padding: 25px;
}
.a-title {
  font-weight: 500;
  font-size: 50px;
}
.a-sub {
  margin: 30px 0px;
  width: 60%;
  font-size: 25px;
  color: violet;
}

.a-desc {
  font-size: 20px;
  width: 60%;
  color: lightgreen;
}
.a-sub {
  background-image: linear-gradient(
    -225deg,
    #231557 3%,
    #44107a 29%,
    #ff1361 67%,
    #fff800 100%
  );
  background-size: auto auto;
  background-clip: border-box;
  background-size: 20% auto;
  color: #fff;
  background-clip: text;
  text-fill-color: transparent;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: textclip 8s linear infinite;
  display: inline-block;
  font-size: 20px;
}

@keyframes textclip {
  to {
    background-position: 200% center;
  }
}

@media screen and (max-width: 480px) {
  .a {
    flex-direction: column;
    text-align: center;
  }
  .a-img {
    padding-top: 40%;
  }
  .a-desc {
    padding: 60px;
  }
  .a-left {
    width: 100%;
  }

  .a-card {
    height: 30vh;
  }
  .a-card.bg {
    display: none;
  }
  .a-right {
    padding: 20px;
  }
}
