.t {
  width: 51px;
  height: 25px;
  border-radius: 22px;
  border: 1pz solid #999999;
  background-color: skyblue;
  position: fixed;
  top: 7px;
  left: 25px;
  z-index: 999;
  align-items: center;
  justify-content: space-around;
}

.t-icons {
  width: 23px;
  height: 23px;
}

.t-button {
  width: 25px;
  height: 20px;
  border-radius: 50%;
  background-color: #999999;
  position: absolute;
  cursor: pointer;
  top: 2px;
}
