.i {
  display: flex;
  height: 100vh;
}

.i-left {
  flex: 1;
  display: flex;
  align-item: center;
  justify-content: center;
}

.i-right {
  flex: 1;
  position: relative;
}
.i-left-wrapper {
  padding: 50px;
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.i-intro {
  font-size: 30px;
  font-weight: 300;
}
.i-name {
  font-size: 60px;
}
.i-title {
  height: 40px;
  overflow: hidden;
}

.i-title-wrapper {
  height: 100%;
  animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
  25% {
    transform: translateY(-50px);
  }
  50% {
    transform: translateY(-100px);
  }
  75% {
    transform: translateY(-150px);
  }
  100% {
    transform: translateY(-200px);
  }
}
.i-title-item {
  height: 50px;
  font-size: 30px;
  font-weight: bold;
  color: skyblue;
  display: flex;
  align-items: center;
}
.i-img {
  width: 80%;
  height: 80%;
  object-fit: contain;
  padding-left: 25%;
  position: absolute;
  padding-top: 35px;
  border-radius: 4px;
  border-radius: 50%;
}

@media screen and (max-width: 480px) {
  .i {
    flex-direction: column;
  }
  .i-name {
    padding-bottom: 5%;
  }

  .i-img {
    padding-top: 95%;
    padding-left: 10%;
  }
  .i-intro {
    padding-top: 40%;
  }

  .i-left-wrapper {
    padding: 10px;
    text-align: center;
    height: 100%;
  }
  .i-desc {
    display: none;
  }
}
