.a {
  display: flex;
  height: 100vh;
  align-items: center;
}
.a-left {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100%;
}

.a-card {
  width: 60%;
  height: 70vh;
  border-radius: 30px;
  position: absolute;
  overflow: hidden;
}

.a-img {
  width: 60%;
  height: 60%;
  position: relative;
  border-radius: 15px 50px 35px;
  padding: 25px;
}
.a-title {
  font-weight: 500;
  font-size: 50px;
}
.a-sub {
  margin: 30px 0px;
  width: 60%;
  font-size: 25px;
  color: violet;
}

.a-desc {
  font-size: 20px;
  width: 60%;
  color: lightgreen;
}
.a-sub {
  background-image: linear-gradient(
    -225deg,
    #231557 3%,
    #44107a 29%,
    #ff1361 67%,
    #fff800 100%
  );
  background-size: auto auto;
  background-clip: border-box;
  background-size: 20% auto;
  color: #fff;
  background-clip: text;
  text-fill-color: transparent;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-animation: textclip 8s linear infinite;
          animation: textclip 8s linear infinite;
  display: inline-block;
  font-size: 20px;
}

@-webkit-keyframes textclip {
  to {
    background-position: 200% center;
  }
}

@keyframes textclip {
  to {
    background-position: 200% center;
  }
}

@media screen and (max-width: 480px) {
  .a {
    flex-direction: column;
    text-align: center;
  }
  .a-img {
    padding-top: 40%;
  }
  .a-desc {
    padding: 60px;
  }
  .a-left {
    width: 100%;
  }

  .a-card {
    height: 30vh;
  }
  .a-card.bg {
    display: none;
  }
  .a-right {
    padding: 20px;
  }
}

.c {
  height: 100vh;
  position: relative;
}

.c-bg {
  width: 20px;
  height: 100%;
  background-color: skyblue;
  position: absolute;
}
.c-wrapper {
  padding: 50px;
  display: flex;
}
.c-left {
  flex: 1 1;
}
.c-right {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.c-title {
  font-size: 50px;
  width: 50%;
}
.c-info-item {
  display: flex;
  align-items: center;
  margin: 50px 0px;
  font-weight: 300;
  width: 70%;
}
.c-icon {
  width: 40px;
  height: 40px;
  margin-right: 30px;
}

form {
  margin-top: 20px;
}
input {
  width: 50%;
  height: 50px;
  border: none;
  border-bottom: 1px solid black;
  margin: 10px 0px;
  font-size: 14px;
  padding-left: 10px;
}
textarea {
  width: 100%;
  margin: 10px 0px;
  font-size: 14px;
  padding-left: 10px;
}

button {
  border: none;
  padding: 15px;
  background-color: skyblue;
  color: salmon;
  font-weight: 500;
  cursor: pointer;
}

@media screen and (max-width: 480px) {
  .c-wrapper {
    flex-direction: column;
    padding: 0px 50px;
  }
  .c-title {
    font-size: 30px;
  }
  .c-info-item {
    margin: 20px 0px;
    width: 100%;
  }
  form {
    margin-top: 0px;
    display: flex;
    flex-wrap: wrap;
  }

  input {
    width: 35%;
    height: 40px;
    margin: 10px;
    margin-left: 0;
  }
  button {
    margin-top: 10px;
  }
}

.i {
  display: flex;
  height: 100vh;
}

.i-left {
  flex: 1 1;
  display: flex;
  align-item: center;
  justify-content: center;
}

.i-right {
  flex: 1 1;
  position: relative;
}
.i-left-wrapper {
  padding: 50px;
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.i-intro {
  font-size: 30px;
  font-weight: 300;
}
.i-name {
  font-size: 60px;
}
.i-title {
  height: 40px;
  overflow: hidden;
}

.i-title-wrapper {
  height: 100%;
  -webkit-animation: move 10s ease-in-out infinite alternate;
          animation: move 10s ease-in-out infinite alternate;
}

@-webkit-keyframes move {
  25% {
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
  }
  50% {
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
  }
  75% {
    -webkit-transform: translateY(-150px);
            transform: translateY(-150px);
  }
  100% {
    -webkit-transform: translateY(-200px);
            transform: translateY(-200px);
  }
}

@keyframes move {
  25% {
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
  }
  50% {
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
  }
  75% {
    -webkit-transform: translateY(-150px);
            transform: translateY(-150px);
  }
  100% {
    -webkit-transform: translateY(-200px);
            transform: translateY(-200px);
  }
}
.i-title-item {
  height: 50px;
  font-size: 30px;
  font-weight: bold;
  color: skyblue;
  display: flex;
  align-items: center;
}
.i-img {
  width: 80%;
  height: 80%;
  object-fit: contain;
  padding-left: 25%;
  position: absolute;
  padding-top: 35px;
  border-radius: 4px;
  border-radius: 50%;
}

@media screen and (max-width: 480px) {
  .i {
    flex-direction: column;
  }
  .i-name {
    padding-bottom: 5%;
  }

  .i-img {
    padding-top: 95%;
    padding-left: 10%;
  }
  .i-intro {
    padding-top: 40%;
  }

  .i-left-wrapper {
    padding: 10px;
    text-align: center;
    height: 100%;
  }
  .i-desc {
    display: none;
  }
}

.pl {
  padding: 50px 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.pl-title {
  font-size: 50px;
  font-weight: 600;
}

.pl-desc {
  margin: 20px 0px;
}

.pl-list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
@media screen and (max-width: 480px) {
  .pl {
    padding: 20px;
  }

  .pl-desc {
    display: none;
  }
}

.p {
  width: 300px;
}
.p {
  width: 30%;
  height: 25vh;
  margin: 20px 10px;
  border: 2px solid rgb(243, 242, 242);
  border-radius: 10px 10px 0px 0px;
  overflow: hidden;
}
.p-browser {
  height: 20px;
  background-color: rgb(243, 242, 242);
  display: flex;
  align-items: center;
}

.p-circle {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  margin: 3px;
  background-color: white;
}
.p-img {
  width: 100%;
  transition: all 10s ease;
}
.p:hover .p-img {
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}
@media screen and (max-width: 480px) {
  .p {
    width: 40%;
    height: 10.5vh;
  }
}

.t {
  width: 51px;
  height: 25px;
  border-radius: 22px;
  border: 1pz solid #999999;
  background-color: skyblue;
  position: fixed;
  top: 7px;
  left: 25px;
  z-index: 999;
  align-items: center;
  justify-content: space-around;
}

.t-icons {
  width: 23px;
  height: 23px;
}

.t-button {
  width: 25px;
  height: 20px;
  border-radius: 50%;
  background-color: #999999;
  position: absolute;
  cursor: pointer;
  top: 2px;
}

